.App {
  width: 100%;
  min-height: 100vh;
  background: linear-gradient(45deg, #22046b, #e14e42);
  color: #fff;
  position: relative;
  padding-top: 30px;
  box-sizing: border-box;
  animation-name: fondo;
  animation-timing-function: linear;
  animation-duration: 4s;
  animation-iteration-count: infinite;
}

@keyframes fondo {
  0% {background: linear-gradient(45deg, #22046b, #e14e42);}
  25% {background: linear-gradient(45deg, #250479, #df493b);}
  50% {background: linear-gradient(45deg, #2e0594, #d62717);}
  75% {background: linear-gradient(45deg, #250479, #df493b);}
  100% {background: linear-gradient(45deg, #22046b, #e14e42);}
}

.navbar {
  width: 100%;
  height: 60px;
  display: flex;
  align-items: center;
  background: rgba(29,33,61, 0.5);
  padding: 0 30px;
  box-sizing: border-box;
}

.logo {
  width: 155px;
}

.logo img {
  width: 100px;
  position: relative;
  z-index: 10;
  cursor: pointer;
  filter: invert(100%);
}

nav {
  flex: 1;
}

nav ul li {
  list-style: none;
  display:inline-block;
  margin-right: 60px;
}

nav ul li a{
  text-decoration: none;
  color: #fff;
  font-size: 14px;
  font-weight: bold;
}

.sidebar {
  width: 155px;
  height: 100vh;
  background: rgba(29,31,58, 0.5);
  position: absolute;
  top: 0;
  left:0;
}

.sidemenu {
  width: 20px;
  display:block;
  margin: 220px auto 0;
  text-align: center;
}

.star1 {
  width: 20px;
  position: absolute;
  left: 258px;
  bottom: 188px;
  opacity: 0.7;
  filter: invert(0.8);
  transform: rotate(14deg);
}

.star2 {
  width: 25px;
  position: absolute;
  left: 200px;
  bottom: 100px;
  opacity: 0.3;
  transform: rotate(60deg);
  filter: invert(90%);
}

.star3 {
  width: 25px;
  position: absolute;
  right: 50px;
  top: 200px;
  opacity: 0.5;
  transform: rotate(45deg);
}